// app/models/create/page.tsx
"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import Link from "next/link";
import { v4 as uuidv4 } from "uuid";
import html2pdf from "html2pdf.js";

type EducationModel = {
  id: string;
  name: string;
  philosophy: string;
  evaluationFocus: string;
  languageFocus: string;
  childFocus: string;
  updatedAt: string;
};

type EducationHistory = EducationModel & {
  note: string;
};

export default function CreateModelPage() {
  const router = useRouter();

  const [models, setModels] = useState<EducationModel[]>([]);
  const [form, setForm] = useState({
    name: "",
    philosophy: "",
    evaluationFocus: "",
    languageFocus: "",
    childFocus: "",
    note: "",
  });
  const [editId, setEditId] = useState<string | null>(null);
  const [error, setError] = useState("");

  // localStorage „Åã„ÇâÊó¢Â≠ò„É¢„Éá„É´„ÇíË™≠„ÅøËæº„Åø
  useEffect(() => {
    const stored = localStorage.getItem("styleModels");
    if (stored) setModels(JSON.parse(stored));
  }, []);

  const handleChange = (field: keyof typeof form, value: string) => {
    setForm((prev) => ({ ...prev, [field]: value }));
  };

  // Google Drive „Å∏PDF„Ç¢„ÉÉ„Éó„É≠„Éº„ÉâÈñ¢Êï∞
  async function uploadPdfToDrive(blob: Blob, filename: string) {
    const accessToken = process.env.NEXT_PUBLIC_GOOGLE_DRIVE_ACCESS_TOKEN;
    if (!accessToken) throw new Error("Google Drive„Ç¢„ÇØ„Çª„Çπ„Éà„Éº„ÇØ„É≥„ÅåË®≠ÂÆö„Åï„Çå„Å¶„ÅÑ„Åæ„Åõ„Çì");

    const metadata = {
      name: filename,
      mimeType: "application/pdf",
    };

    const formData = new FormData();
    formData.append("metadata", new Blob([JSON.stringify(metadata)], { type: "application/json" }));
    formData.append("file", blob);

    const response = await fetch(
      "https://www.googleapis.com/upload/drive/v3/files?uploadType=multipart&fields=id,webViewLink",
      {
        method: "POST",
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
        body: formData,
      }
    );

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(`Google Drive„Ç¢„ÉÉ„Éó„É≠„Éº„ÉâÂ§±Êïó: ${errorText}`);
    }

    return response.json();
  }

  const handleSave = async () => {
    setError("");
    if (
      !form.name.trim() ||
      !form.philosophy.trim() ||
      !form.evaluationFocus.trim() ||
      !form.languageFocus.trim() ||
      !form.childFocus.trim()
    ) {
      setError("„Åô„Åπ„Å¶„ÅÆÂøÖÈ†àÈ†ÖÁõÆ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ");
      return;
    }

    const now = new Date().toISOString();
    let updatedModels: EducationModel[];

    if (editId) {
      updatedModels = models.map((m) =>
        m.id === editId
          ? { ...m, ...form, updatedAt: now }
          : m
      );
    } else {
      updatedModels = [
        {
          id: uuidv4(),
          name: form.name.trim(),
          philosophy: form.philosophy.trim(),
          evaluationFocus: form.evaluationFocus.trim(),
          languageFocus: form.languageFocus.trim(),
          childFocus: form.childFocus.trim(),
          updatedAt: now,
        },
        ...models,
      ];
    }

    // 1) localStorage „Å´‰øùÂ≠ò
    localStorage.setItem("styleModels", JSON.stringify(updatedModels));
    setModels(updatedModels);

    // 2) Â±•Ê≠¥„Éá„Éº„Çø„Çí localStorage „Å´ËøΩÂä†
    const newHistoryEntry: EducationHistory = {
      id: editId || updatedModels[0].id,
      name: form.name.trim(),
      philosophy: form.philosophy.trim(),
      evaluationFocus: form.evaluationFocus.trim(),
      languageFocus: form.languageFocus.trim(),
      childFocus: form.childFocus.trim(),
      updatedAt: now,
      note: form.note.trim() || "ÔºàÊõ¥Êñ∞ÊôÇ„Å´„É°„É¢„Å™„ÅóÔºâ",
    };
    const prevHistory = JSON.parse(localStorage.getItem("educationStylesHistory") || "[]") as EducationHistory[];
    const updatedHistory = [newHistoryEntry, ...prevHistory];
    localStorage.setItem("educationStylesHistory", JSON.stringify(updatedHistory));

    // 3) PDFÁîüÊàêÔºÜDrive‰øùÂ≠ò
    try {
      // PDFÂåñÂØæË±°„ÅÆHTMLË¶ÅÁ¥†„Çí‰∏ÄÊôÇÁîüÊàê
      const tempDiv = document.createElement("div");
      tempDiv.style.padding = "20px";
      tempDiv.style.fontFamily = "sans-serif";
      tempDiv.style.backgroundColor = "#fff";
      tempDiv.style.color = "#000";

      tempDiv.innerHTML = `
        <h1>${form.name.trim()}</h1>
        <h2>ÊïôËÇ≤Ë¶≥</h2>
        <p>${form.philosophy.trim().replace(/\n/g, "<br>")}</p>
        <h2>Ë©ï‰æ°Ë¶≥ÁÇπ„ÅÆÈáçË¶ñÁÇπ</h2>
        <p>${form.evaluationFocus.trim().replace(/\n/g, "<br>")}</p>
        <h2>Ë®ÄË™ûÊ¥ªÂãï„ÅÆÈáçË¶ñÁÇπ</h2>
        <p>${form.languageFocus.trim().replace(/\n/g, "<br>")}</p>
        <h2>ËÇ≤„Å¶„Åü„ÅÑÂ≠ê„Å©„ÇÇ„ÅÆÂßø</h2>
        <p>${form.childFocus.trim().replace(/\n/g, "<br>")}</p>
        <h2>Êõ¥Êñ∞„É°„É¢</h2>
        <p>${form.note.trim().replace(/\n/g, "<br>")}</p>
        <p>Êõ¥Êñ∞Êó•ÊôÇ: ${now}</p>
      `;

      document.body.appendChild(tempDiv);

      const pdfBlob = await html2pdf()
        .from(tempDiv)
        .set({
          margin: 10,
          jsPDF: { unit: "mm", format: "a4", orientation: "portrait" },
          html2canvas: { scale: 2 },
          pagebreak: { mode: ["avoid-all"] },
        })
        .outputPdf("blob");

      document.body.removeChild(tempDiv);

      await uploadPdfToDrive(pdfBlob, `${form.name.trim()}_${now}.pdf`);

      alert("‚úÖ „É≠„Éº„Ç´„É´‰øùÂ≠ò„Åä„Çà„Å≥Google Drive‰øùÂ≠ò„ÅåÂÆå‰∫Ü„Åó„Åæ„Åó„ÅüÔºÅ");
      router.push("/models/history");
    } catch (e) {
      alert("‚ö†Ô∏è PDFÁîüÊàê„Åæ„Åü„ÅØGoogle Drive‰øùÂ≠ò„Åß„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ");
      console.error(e);
    }
  };

  return (
    <main
      style={{
        padding: "2rem 4rem",
        width: "100%",
        maxWidth: 1200,
        margin: "0 auto",
        fontFamily: "sans-serif",
      }}
    >
      {/* „Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥ */}
      <nav
        style={{
          display: "flex",
          gap: 12,
          marginBottom: 24,
          overflowX: "auto",
        }}
      >
        {[
          ["/", "üè† „Éõ„Éº„É†"],
          ["/plan", "üìã ÊéàÊ•≠‰ΩúÊàê"],
          ["/plan/history", "üìñ Ë®àÁîªÂ±•Ê≠¥"],
          ["/practice/history", "üì∑ ÂÆüË∑µÂ±•Ê≠¥"],
          ["/models/create", "‚úèÔ∏è ÊïôËÇ≤Ë¶≥‰ΩúÊàê"],
          ["/models", "üìö ÊïôËÇ≤Ë¶≥‰∏ÄË¶ß"],
          ["/models/history", "üïí ÊïôËÇ≤Ë¶≥Â±•Ê≠¥"],
        ].map(([href, label]) => (
          <Link
            key={href}
            href={href}
            style={{
              padding: "8px 12px",
              backgroundColor: href === "/models/create" ? "#4CAF50" : "#1976d2",
              color: "white",
              borderRadius: 6,
              textDecoration: "none",
              whiteSpace: "nowrap",
            }}
          >
            {label}
          </Link>
        ))}
      </nav>

      <h1
        style={{
          fontSize: "2rem",
          marginBottom: "1.5rem",
          textAlign: "center",
        }}
      >
        {editId ? "‚úèÔ∏è ÊïôËÇ≤Ë¶≥„É¢„Éá„É´„ÇíÁ∑®ÈõÜ" : "‚úèÔ∏è Êñ∞„Åó„ÅÑÊïôËÇ≤Ë¶≥„É¢„Éá„É´„Çí‰ΩúÊàê"}
      </h1>

      {error && (
        <p
          style={{
            color: "red",
            marginBottom: "1rem",
            textAlign: "center",
          }}
        >
          {error}
        </p>
      )}

      <section
        style={{
          backgroundColor: "#f9f9f9",
          padding: 24,
          borderRadius: 8,
          boxShadow: "0 2px 6px rgba(0,0,0,0.1)",
        }}
      >
        <label style={{ display: "block", marginBottom: 12 }}>
          „É¢„Éá„É´ÂêçÔºàÂøÖÈ†àÔºâÔºö
          <input
            type="text"
            value={form.name}
            onChange={(e) => handleChange("name", e.target.value)}
            style={{
              width: "100%",
              padding: "0.8rem",
              fontSize: "1.1rem",
              borderRadius: 6,
              border: "1px solid #ccc",
              marginTop: 4,
            }}
          />
        </label>

        <label style={{ display: "block", marginBottom: 12 }}>
          ÊïôËÇ≤Ë¶≥ÔºàÂøÖÈ†àÔºâÔºö
          <textarea
            rows={2}
            value={form.philosophy}
            onChange={(e) => handleChange("philosophy", e.target.value)}
            style={{
              width: "100%",
              padding: "0.8rem",
              fontSize: "1.1rem",
              borderRadius: 6,
              border: "1px solid #ccc",
              marginTop: 4,
            }}
          />
        </label>

        <label style={{ display: "block", marginBottom: 12 }}>
          Ë©ï‰æ°Ë¶≥ÁÇπ„ÅÆÈáçË¶ñÁÇπÔºàÂøÖÈ†àÔºâÔºö
          <textarea
            rows={2}
            value={form.evaluationFocus}
            onChange={(e) => handleChange("evaluationFocus", e.target.value)}
            style={{
              width: "100%",
              padding: "0.8rem",
              fontSize: "1.1rem",
              borderRadius: 6,
              border: "1px solid #ccc",
              marginTop: 4,
            }}
          />
        </label>

        <label style={{ display: "block", marginBottom: 12 }}>
          Ë®ÄË™ûÊ¥ªÂãï„ÅÆÈáçË¶ñÁÇπÔºàÂøÖÈ†àÔºâÔºö
          <textarea
            rows={2}
            value={form.languageFocus}
            onChange={(e) => handleChange("languageFocus", e.target.value)}
            style={{
              width: "100%",
              padding: "0.8rem",
              fontSize: "1.1rem",
              borderRadius: 6,
              border: "1px solid #ccc",
              marginTop: 4,
            }}
          />
        </label>

        <label style={{ display: "block", marginBottom: 12 }}>
          ËÇ≤„Å¶„Åü„ÅÑÂ≠ê„Å©„ÇÇ„ÅÆÂßøÔºàÂøÖÈ†àÔºâÔºö
          <textarea
            rows={2}
            value={form.childFocus}
            onChange={(e) => handleChange("childFocus", e.target.value)}
            style={{
              width: "100%",
              padding: "0.8rem",
              fontSize: "1.1rem",
              borderRadius: 6,
              border: "1px solid #ccc",
              marginTop: 4,
            }}
          />
        </label>

        <label style={{ display: "block", marginBottom: 24 }}>
          Êõ¥Êñ∞„É°„É¢Ôºà‰ªªÊÑèÔºâÔºö
          <textarea
            rows={2}
            value={form.note}
            onChange={(e) => handleChange("note", e.target.value)}
            style={{
              width: "100%",
              padding: "0.8rem",
              fontSize: "1.1rem",
              borderRadius: 6,
              border: "1px solid #ccc",
              marginTop: 4,
              fontStyle: "italic",
            }}
          />
        </label>

        <div style={{ textAlign: "center" }}>
          <button
            onClick={handleSave}
            style={{
              padding: "0.8rem 2rem",
              fontSize: "1.1rem",
              backgroundColor: "#4CAF50",
              color: "white",
              border: "none",
              borderRadius: 8,
              cursor: "pointer",
            }}
          >
            {editId ? "Êõ¥Êñ∞„Åó„Å¶‰øùÂ≠ò" : "‰ΩúÊàê„Åó„Å¶‰øùÂ≠ò"}
          </button>
        </div>
      </section>
    </main>
  );
}
